
import { defineComponent, ref } from "vue";
import { loader } from "@/utils/loader";
import BaseReportTable from "@/apps/EMC/Components/tables/BaseReportTable.vue";
import { RowActionButtonInterface, TableColumnInterface } from "@uniquedj95/vtable";
import { modal } from "@/utils/modal";
import DrilldownTableVue from "@/apps/EMC/Components/tables/DrilldownTable.vue";
import { ViralLoadReportService } from "@/apps/ART/services/reports/viral_load_report";
import { AGE_GROUPS } from "@/apps/ART/services/reports/patient_report_service";
import HisDate from "@/utils/Date";
import { formatGender, toGenderString } from "@/utils/Strs";
import { sortByARV } from "@/apps/EMC/utils/common";
import { chunk, get } from "lodash";
import { PersonService } from "@/services/person_service";
import router from "@/router";

export default defineComponent({
  name: "VLCoverage",
  components: { BaseReportTable },
  setup() {
    const period = ref("-");
    const rows = ref<any[]>([]);
    const columns: TableColumnInterface[] = [
      { path: "age_group", label: "Age group" },
      { path: "gender", label: "Gender", formatter: toGenderString },
      { path: "tx_curr", label: "TX_CURR", drillable: true },
      { path: "due_for_vl", label: "Due for VL", drillable: true },
      { path: "drawn.routine", label: "Routine (Sample Drawn)", drillable: true },
      { path: "drawn.targeted", label: 'Targeted (Sample Drawn)', drillable: true },
      { path: "high_vl.routine", label: 'Routine (High VL (>=1000 copies))', drillable: true },
      { path: "high_vl.targeted", label: 'Targeted High VL (>=1000 copies)', drillable: true },
      { path: "low_vl.routine", label: 'Routine (Low VL (<1000 copies))', drillable: true },
      { path: "low_vl.targeted", label: 'Targeted (Low VL (<1000 copies))', drillable: true },
    ]

    const buildRow = (data: Record<string, any>, gender: "M" | "F", age_group: string) => {
      return {
        gender,
        age_group,
        ...get(data, `${age_group}.${gender}`, {
          drawn: { routine: [], targeted: [] },
          high_vl: { routine: [], targeted: [] },
          low_vl: { routine: [], targeted: [] },
          tx_curr:  [],
          due_for_vl:  [],
        })
      }
    }

    const fetchData =  async ({ dateRange }: Record<string, any>) => {
      await loader.show()
      const report = new ViralLoadReportService()
      report.setStartDate(dateRange.startDate)
      report.setEndDate(dateRange.endDate)
      period.value = report.getDateIntervalPeriod()
      const data: any = await report.getVLCoverage({ application: "emastercard" })
      const males: any[] = []
      const females: any[] = []
      for(const group of AGE_GROUPS){
        males.push(buildRow(data, "M", group))
        females.push(buildRow(data, "F", group))
      }
      rows.value = [...females, ...males];
      await loader.hide();
    }

    const onDrilldown = async (data: {column: TableColumnInterface; row: any}) => {
      const columns: TableColumnInterface[] = [
        { path: "identifier", label: "ARV Number", preSort: sortByARV, initialSort: true },
        { path: "birthdate", label: "Date of Birth", formatter: HisDate.toStandardHisDisplayFormat },
        { path: "gender", label: "Gender", formatter: formatGender },
      ]

      const rowActionButtons: RowActionButtonInterface[] = [{ 
        label: "View Client", 
        action: (r) => router.push(`/emc/patient/${r['person_id']}`)
      }]
      
      let rows: Array<any> =[];
      const backlog = chunk(data.row[data.column.path] as Array<number>, 1000);
      for (const chunk of backlog) {
        const res = await PersonService.getPersons(chunk);
        rows = rows.concat(res ?? []);
      }

      return modal.show(DrilldownTableVue, {
        title: `${data.row.age_group} ${data.column.label} ${data.row.gender}s`,
        rowActionButtons,
        rows,
        columns,
      })
    }

    return {
      rows,
      columns,
      period,
      fetchData,
      onDrilldown,
    }
  }
})
